import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Headers, Http } from '@angular/http';
import { LocalStorageService } from 'angular-2-local-storage';
import { MsgComponent } from '../msg/msg.component';
import { StatsWidgets } from '../statsWidgets/stats-widgets';

@Component({
  selector: 'app-inbox',
  templateUrl: './inbox.component.html',
  styleUrls: ['./inbox.component.css']
})
export class InboxComponent implements OnInit {

  primljenePoruke = [];
  selectedMessage:any;
  brojPoruka:any;
  user:any;
  constructor(private http: Http, private localStorageService: LocalStorageService, private router: Router) { }

  ngOnInit() {
    this.http.get(`sessions/LogovaniKorisnik`)
        .map(res => res.json())
        .subscribe(data => {
          this.user = data;
          if (this.user.success === undefined) {
              this.getMessages();
          }

        },
        err => console.log(err),
        () => console.log('Completed'));
  }


  getMessages() {
    this.http.get('messages/getMessages')
      .map(res => res.json())
      .subscribe(data => {
        this.primljenePoruke = data;
        this.brojPrimljenih();
      },
      err => console.log(err),
      () => console.log('Completed'));

  }
  brojPrimljenih() {
    var poruke = this.primljenePoruke.filter(x => x.read == 1);
    this.brojPoruka = poruke.length;
    console.log(poruke.length);
  }
  goToMessage(poruka:any){
    this.selectedMessage = {id:poruka.id};
    this.router.navigate(['./msg',this.selectedMessage]);
  }

}



