import { Component, OnInit } from '@angular/core';

import { Headers, Http } from '@angular/http';
import { LocalStorageService } from 'angular-2-local-storage';

import { RouterModule, Routes, Router } from '@angular/router';
import { PermService } from "../services/perm-service.service";
declare var google: any;

@Component({
  selector: 'app-update-plantation',
  templateUrl: './updatePlant.html',
  styleUrls: ['./update-plantation.component.css']
})
export class UpdatePlantation implements OnInit {
  vidljivost = false;
  idPlant: any;
  vrste: any;
  dozvole: any;
  user: any;
  imePlant: any;
  imePodT: any;
  imePS: any;
  uspesnoPromenjeniPodaci = false;
  tipovi: any;
  nazivTip: any;
  idTip: any;
  idT: number;
  podtipovi: any;
  mapProp = null;
  map = null;
  drawingManager = null;
  constructor(private permService: PermService, private http: Http, private localStorageService: LocalStorageService, private router: Router) {
  }

  callType(value) {
    this.nazivTip = value;
    window.alert(this.nazivTip);
    this.http.get(`users/prikaziIDTipa/${this.nazivTip}`)
      .map(res => res.json())
      .subscribe(data => {
        this.idTip = data;
        this.idT = this.idTip[0].id;
        this.popuniPodtip(this.idT);
      },
      err => console.log(err),
      () => console.log('Completed'));
  }


  callType2(value) {
    this.imePodT = value;
    this.popuniProducer(this.imePodT);
  }

  initMap() {
    let self = this;
    self.map = new google.maps.Map(document.getElementById('googleMap'), {
      center: { lat: 44.017813, lng: 20.907229 },
      zoom: 7
    });
    alert("u initu!");
    self.drawingManager = new google.maps.drawing.DrawingManager({
      drawingControl: true,
      drawingControlOptions: {
        position: google.maps.ControlPosition.TOP_CENTER,
        drawingModes: ['polygon'],
      },
      polygonOptions: {
        clickable: true,
        editable: true,
        zIndex: 1
      }

    });
    self.drawingManager.setMap(self.map);
  }

  ngOnInit() {
    this.permService.getPerms().subscribe(data => {
      this.dozvole = data;
    },
      err => console.log(err),
      () => console.log('Completed'));
    this.idPlant = this.localStorageService.get(`idPlantaze`);
    this.http.get(`sessions/LogovaniKorisnik`)
      .map(res => res.json())
      .subscribe(data => {
        this.user = data;
        alert("usao");
        if (this.user.success === undefined) {
          this.vidljivost = true;
          this.popuniPolja();
          this.nadjiTip(this.user.id);
          this.initMap();
          alert("dole!");
        }


      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  popuniPolja() {
    var i: number;
    this.http.get(`users/prikaziPodatkeOPlant/${this.idPlant}/${this.user.id}`)
      .map(res => res.json())
      .subscribe(data => {
        this.vrste = data;
        this.imePlant = this.vrste[0].name;
        this.imePS = this.vrste[0].namep;
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  promeniPodatke() {
    var novo: any;
    /*${this.imeTip}*/
    this.http.get(`users/promeniPlantaze/${this.idPlant}/${this.user.id}/${this.imePlant}/${this.nazivTip}/${this.imePodT}/${this.imePS}`)
      .map(res => res.json())
      .subscribe(data => {
        novo = data;
        if (novo.success) {
          this.uspesnoPromenjeniPodaci = true;
          setTimeout(() => {
            this.uspesnoPromenjeniPodaci = false;
          }, 3000);

        }

      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  nadjiTip(value) {
    this.http.get(`users/vratiTipove/${value}`)
      .map(res => res.json())
      .subscribe(data => {
        this.tipovi = data;
        this.popuniPodtip(this.tipovi[0].id);
      },
      err => console.log(err),
      () => console.log('Completed'));
  }


  popuniPodtip(value) {
    this.http.get(`users/prikaziPodtip/${value}`)
      .map(res => res.json())
      .subscribe(data => {
        this.podtipovi = data;
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  popuniProducer(value) {
    var imePS1: any;
    this.http.get(`users/prikaziProizvodjaca/${value}`)
      .map(res => res.json())
      .subscribe(data => {
        imePS1 = data;
        this.imePS = imePS1[0].name;
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

}



