import { Component } from '@angular/core';

import { Headers, Http } from '@angular/http';
import { MeniComponent } from "../meni/meni.component";
import { PermService } from "../services/perm-service.service";
import { FilterPipe } from '../filter.pipe';
import { StatsWidgets } from '../statsWidgets/stats-widgets';


@Component({
    moduleId: module.id,
    selector: 'app-root',
    templateUrl: "./index.html"


})
export class AddWorker {
    data: any;
    vidljivost = false;
    meniVidljiv = false;
    user: any;
    roles: any;
    c: any;
    k: any;
    i: any;
    selectedWorker: any;
    selectedRole: any;
    selektovan: any;
    vrste: string[];
    binary: any;
    usersWorkers: any;
    updateRole: any;
    workerID: any;
    updatedRole = false;
    dataa: any;
    dozvole: any;
    stats: any;
    uspesnoDodatRadnik = false;
    uspesnoIzbrisanRadnik = false;
    username: any;
    usersid: any;
    usernameExists = false;
    radnikVecZaposlen = false;
    radnikNepostoji = false;
    radnikJeVlasnik = false;
    constructor(private permService: PermService, private http: Http) {

    }

    onChangeRoleUpdate(roleID, workerID) {
        this.updateRole = roleID;
        this.updatedRole = true;

        this.workerID = workerID;
        this.update();
        console.log(this.updateRole);

    }
    onChangeW(newValue) {
        this.selectedWorker = newValue;
        console.log(this.selectedWorker);
        console.log(this.user.id);
    }
    
    onChangeR(newValue) {
        this.selektovan = true;
        this.binary = {};
        this.i = 0;

        this.selectedRole = newValue;
        var n = this.selectedRole;
        var roleValue;
        // foreach petlja za uloge    
        this.roles.forEach(function (data) {
            if (data.id == n)
                roleValue = data.permission

        });
        // uzmi binarne vrednosti dozvola
        for (this.c = 3; this.c >= 0; this.c--) {
            this.k = roleValue >> this.c;

            if (this.k & 1)
                this.binary[this.i] = 1;
            else
                this.binary[this.i] = 0;
            this.i = this.i + 1;
        }
        console.log(this.binary);

    }
    meniToggle() {
        this.meniVidljiv = !this.meniVidljiv;
    }
    ngOnInit() {
        this.permService.getPerms().subscribe(data => {
            this.dozvole = data;
        },
            err => console.log(err),
            () => console.log('Completed'));
        console.log("log");
        this.getUsers();
        this.getRoles();

        this.http.get(`sessions/LogovaniKorisnik`)
            .map(res => res.json())
            .subscribe(data => {
                this.user = data;
                console.log(this.user);
                if (this.user.success === undefined) {
                    this.vidljivost = true;
                    this.popuniTabelu();
                }

            },
            err => console.log(err),
            () => console.log('Completed'));

    }
    getUsers() {
        console.log("userrr");
        this.http.get(`users/getUsers`)
            .map(res => res.json())
            .subscribe(data => {
                this.data = data;
                console.log(this.data[1]);
                console.log(this.data);
            },
            err => console.log(err),
            () => console.log('Completed'));

    }
    getUsersWorkers() {
        console.log("userrr worker");
        this.http.get(`users/getUsersWorkers/${this.user.id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.usersWorkers = data;
                console.log("worker user " + this.usersWorkers);

            },
            err => console.log(err),
            () => console.log('Completed'));

    }
    getRoles() {
        console.log("userrr");
        this.binary = "";
        this.http.get(`users/getRoles`)
            .map(res => res.json())
            .subscribe(data => {
                this.roles = data;

                console.log("roles", this.roles);
            },
            err => console.log(err),
            () => console.log('Completed'));

    }
    popuniTabelu() {
        this.http.get(`users/prikaziPlantaze/${this.user.id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.vrste = data;

            },
            err => console.log(err),
            () => console.log('Completed'));
        this.getUsersWorkers();
    }
    dodajRadnika() {
        this.http.get(`users/dodajRadnika2/${this.user.id}/${this.username}/${this.selectedRole}`)
            .map(res => res.json())
            .subscribe(data => {
                this.dataa = data
                console.log(this.dataa);
                if (this.dataa.success) {
                    this.uspesnoDodatRadnik = true;

                }
                else {
                    this.uspesnoDodatRadnik = false;
                    if (this.dataa[0] == "ima") {
                        this.radnikVecZaposlen = true;
                        setTimeout(() => {
                            this.radnikVecZaposlen = false;
                        }, 3000);
                    }
                    else if (this.dataa[0] == "nema") {
                        this.radnikNepostoji = true;
                        setTimeout(() => {
                            this.radnikNepostoji = false;
                        }, 3000);
                    }
                    else if (this.dataa[0] == "vlasnik") {
                        this.radnikJeVlasnik = true;
                        setTimeout(() => {
                            this.radnikJeVlasnik = false;
                        }, 3000);
                    }
                }
            },
            err => console.log(err),
            () => console.log('Completed'));
        this.getUsersWorkers();


    }
    delete(id) {
        console.log(id);

        this.http.get(`users/deleteWorkers/${id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.dataa = data

                this.uspesnoIzbrisanRadnik = true;

            },
            err => console.log(err),
            () => console.log('Completed'));
        this.getUsersWorkers();
    }
    update() {


        this.http.get(`users/updateWorkers/${this.workerID}/${this.updateRole}`)
            .map(res => res.json())
            .subscribe(data => {
                this.dataa = data



            },
            err => console.log(err),
            () => console.log('Completed'));
        this.getUsersWorkers();

    }
}


