import { Component, OnInit } from '@angular/core';
import { Headers, Http } from '@angular/http';
import { RouterModule, Routes, Router } from '@angular/router';

@Component({
  selector: 'app-admin-page',
  templateUrl: './adminPage.html',
  styleUrls: ['./admin-page.component.css']
})
export class AdminPageComponent implements OnInit {
  user: any;
  vidljivost = false;
  prikaziUsere = false;
  prikaziZahtev = false;
  uspesno = false;
  sviKorisnici: any;
  sviZahtevi: any;
  constructor(private http: Http, private router: Router) { }

  ngOnInit() {
  }

  prikaziSve() {

    this.http.get(`users/prikaziKorisnike`)
      .map(res => res.json())
      .subscribe(data => {
        this.sviKorisnici = data;
        this.prikaziUsere = true;
        this.prikaziZahtev = false;
      },
      err => console.log(err),
      () => console.log('Completed'));
  }

  prikaziZahteve() {
    this.http.get(`users/prikaziSveZahteve`)
      .map(res => res.json())
      .subscribe(data => {
        this.sviZahtevi = data;
        this.prikaziZahtev = true;
        this.prikaziUsere = false;
      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  Dozvoli(value) {
    var id = value;
    var pom1: any;
    var pom2: any;
    this.http.get(`users/dozvolaZaVlasnika/${id}`)
      .map(res => res.json())
      .subscribe(data => {
        pom1 = data;
        if (pom1.success == true) {

          this.http.get(`users/dozvolaZaVlasnika1/${id}`)
            .map(res => res.json())
            .subscribe(data => {
              pom2 = data;
              if (pom2.success == true) {
                this.uspesno = true;
                this.prikaziZahteve();
              }
            },
            err => console.log(err),
            () => console.log('Completed'));
        }
      },
      err => console.log(err),
      () => console.log('Completed'));

  }

  Ignorisi(value) {
    var pom3:any;
      this.http.get(`users/obrisiZahtev/${value}`)
      .map(res => res.json())
      .subscribe(data => {
        pom3=data;
        if(pom3.success==true) {
        this.uspesno=true;
        this.prikaziZahteve();
        }
      },
      err => console.log(err),
      () => console.log('Completed'));
    
  }

}



