import { Component, Input, OnInit } from '@angular/core';
import { Plantation, Vrsta, Proizvodjac } from './plantation';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms'
import 'rxjs/add/operator/finally';
import { Observable } from 'rxjs/Observable';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import { MeniComponent } from "../meni/meni.component";
import { PlantationService } from './plantation.service';
import { PermService } from "../services/perm-service.service";
import { StatsWidgets } from '../statsWidgets/stats-widgets';
declare var google: any;


@Component({
    selector: 'plantation-form',
    //templateUrl:'./plantation.form.component.html',
    templateUrl: './addparcel.html'

})
export class AddPlantation implements OnInit {
    plantForm: FormGroup;

    user: any;

    dozvole: any;
    imanjeVidljivo: boolean = false;
    plantazaVidljiva: boolean = true;
    @Input() plantation: Plantation;
    @Input() Tip: Vrsta;
    @Input() proizvodjac: Proizvodjac;
    idVlasnika = [];
    ddVisible = false;
    test: any;
    tipovi = [];
    podtipovi = [];
    proizvodjaci=[];
    sviPodtipovi = [];
    nizPoligona = [];
    nizOverlay = [];
    sviVlasnici = [];
    dozvoleKodGazde = [];
    isLoading = false;
    smeDaPise = false;
    dodatProizvodjac: boolean = false;
    dodatTip: boolean = false;
    dodatPodtip: boolean = false;
    dodataPlantaza: boolean = false;
    mapProp = null;
    map = null;
    imanjeIme: string = "";
    drawingManager = null;
    stats: any;
    isOwner: boolean;
    komeMozeDaDoda: any = [];
    perm = [];
    imanja: any;
    dodatoImanje: boolean = false;

    constructor(private permService: PermService, private fb: FormBuilder, private plantationService: PlantationService, private http: Http) {
        this.createForm();
    }
    promeniTab() {
        this.plantazaVidljiva = !this.plantazaVidljiva;
        this.imanjeVidljivo = !this.imanjeVidljivo;
    }
    uzmiImanja() {
        this.http.get(`users/uzmiImanja`)
            .map(res => res.json())
            .subscribe(data => {
                this.imanja = data;


            },
            err => console.log(err),
            () => console.log('Completed'));
    }

    cleanInput() {

        for (var i = 0; i < this.nizPoligona.length; i++) {
            this.nizPoligona[i] = null;
            this.nizOverlay[i].setMap(null);
        }
        this.nizPoligona = [];
        this.nizOverlay = [];
    }

    createForm() {
        this.plantForm = this.fb.group({
            name: ['', Validators.required],
            vrsta: ['', Validators.required],
            podvrsta: [''],
            proizvodjacSemena: [''],
            vlasnikPlantaze: ['']
        });
    }


    getUser() {
        this.http.get(`sessions/LogovaniKorisnik`)
            .map(res => res.json())
            .subscribe(data => {
                this.user = data;
                console.log(this.user);


            },
            err => console.log(err),
            () => console.log('Completed'));
    }
    
    dozvoleKodVlasnika() {

    }
    dodajImanje() {

        var idvlasnika;
        if (this.isOwner) idvlasnika = this.user.id;
        else {
            idvlasnika = document.getElementById("vlasnik");
            idvlasnika = idvlasnika.options[idvlasnika.selectedIndex].value;
            if (idvlasnika == "" || idvlasnika === undefined || idvlasnika === null) {
                alert("Mora vlasnik da se izabere bajo");
                return;
            }
        }
        this.http.get(`users/dodajImanje/${this.imanjeIme}/${idvlasnika}`)
            .map(res => res.json())
            .subscribe(data => {
                if (this.dodatoImanje == true) {
                    setTimeout(() => {
                        this.dodatoImanje = false;
                    }, 3000);
                }
                this.dodatoImanje = data;
                this.uzmiImanja();


            },
            err => console.log(err),
            () => console.log('Completed'));


    }
    close() {
        this.dodatoImanje = false;
    }

    addPlantation(im) {


        var idOw;
        const model = this.plantForm.value;

        if (this.isOwner)
            idOw = this.user.id;
        else
            idOw = model.vlasnikPlantaze;




        this.http.get(`types/addPlantation/${idOw}/${model.name}/${model.vrsta}/${model.podvrsta}/${model.proizvodjacSemena}/${im}/${JSON.stringify(this.nizPoligona)}`)
            .map(res => res.json())
            .subscribe(data => {
                this.test = data;
                if (this.test.success) {
                    this.dodataPlantaza = true;
                }
                else {
                    this.dodataPlantaza = false;
                }
            },
            err => console.log(err),
            () => console.log('Completed'));
        this.plantForm.reset();


        this.cleanInput();
    }

    onSubmit() {
        var im;
        im = document.getElementById("imanje");
        if (im == "" || im === null || im === undefined) {
            alert("imanje bato!");
            return;
        }
        if (im.options == "" || im.options === null || im.options === undefined) {
            alert("imanje bato!");
            return;
        }
        if (im.options[im.selectedIndex] == "" || im.options[im.selectedIndex] === null || im.options[im.selectedIndex] === undefined) {
            alert("imanje bato!");
            return;
        }
        im = (im.options[im.selectedIndex]).value;
        if (im == "" || im === null || im === undefined) {
            alert("imanje bato!");
            return;
        }
        const model = this.plantForm.value;
        if (this.nizPoligona[0] == null) {
            alert("fali ti poligon!" + this.plantForm.value.name);
        }
        else {
            if (this.plantForm.value.name == null || this.plantForm.value.name == "") {
                alert("fali IME" + this.plantForm.value.name);
            }
            else if (model.vrsta == null || model.podvrsta == null || model.proizvodjacSemena == null || model.vrsta == "" || model.podvrsta == "" || model.proizvodjacSemena == "") {
                alert("vrsta/podvrsta/proizvodjac");

            }
            else if (!this.isOwner && (model.vlasnikPlantaze == null || model.vlasnikPlantaze == '')) {
                alert("unesite ime vlasnika plantaze" + this.plantForm.value.vlasnikPlantaze);
            }
            else {

                this.addPlantation(im);
            }

        }


    };

    userIsOwner() {
        this.http.get(`permisions/userIsOwner`)
            .map(res => res.json())
            .subscribe(data => {
                this.isOwner = data.owner;
                if (!this.isOwner) {
                    this.getOwners();
                }
                else {
                    this.tipovi = this.getTypes(this.user.id);
                }
            },
            err => console.log(err),
            () => console.log('Completed'));
    }

    getOwners() {
        this.http.get(`users/getOwners`)
            .map(res => res.json())
            .subscribe(data => {
                this.sviVlasnici = data;
                this.getPerms();
            },
            err => console.log(err),
            () => console.log('Completed'));
    }

    getPerms() {
        this.http.get(`permisions/dozvoleKodVlasnika`)
            .map(res => res.json())
            .subscribe(data => {
                console.log(data.success + " " + data.dozvole)
                this.perm = data;
                if (data.dozvole.length > 0) {
                    console.log()
                    this.dozvoleKodGazde = data.dozvole.filter(x => x.vrednosti.plantCRUD == true);

                    if (this.dozvoleKodGazde.length > 0) {
                        console.log("broj sa dozvolom kod gazde " + this.dozvoleKodGazde[0].vlasnik);
                        this.spoji();
                        this.smeDaPise = true;
                    }
                }
            },
            err => console.log(err),
            () => console.log('Completed'));

    }
    spoji() {
        console.log(this.sviVlasnici.length + " broj svih vlasnika");
        for (var i = 0; i < this.sviVlasnici.length; i++)
            for (var j = 0; j < this.dozvoleKodGazde.length; j++) {
                console.log(this.sviVlasnici[i].idOwner + " " + this.dozvoleKodGazde[j].vlasnik);
                if (this.sviVlasnici[i].idOwner == this.dozvoleKodGazde[j].vlasnik) {

                    var noviG = {
                        id: this.sviVlasnici[i].id,
                        name: this.sviVlasnici[i].name,
                        surname: this.sviVlasnici[i].surname,
                        username: this.sviVlasnici[i].username,
                        email: this.sviVlasnici[i].email
                    };
                    this.komeMozeDaDoda.push(noviG);
                    this.idVlasnika.push(this.sviVlasnici[i].idOwner);
                }
            }
        this.tipovi = this.getTypes(this.idVlasnika);

    }

    ngOnInit(): void {
        setTimeout(() => {
            console.log("timeout");
        }, 1000);
        this.permService.getPerms().subscribe(data => {
            this.dozvole = data;
        });

        let self = this;

        self.http.get(`sessions/LogovaniKorisnik`)
            .map(res => res.json())
            .subscribe(data => {
                this.user = data;
                console.log(this.user);
                if (this.user.id) {
                    this.getArrays();
                    this.userIsOwner();

                }
                this.uzmiImanja();
            },
            err => console.log(err),
            () => console.log('Completed'));

        if (document.getElementById('googleMap') == undefined || document.getElementById('googleMap') == null) return;

        self.map = new google.maps.Map(document.getElementById('googleMap'), {
            center: { lat: 44.017813, lng: 20.907229 },
            zoom: 7
        });

        self.drawingManager = new google.maps.drawing.DrawingManager({
            drawingControl: true,
            drawingControlOptions: {
                position: google.maps.ControlPosition.TOP_CENTER,
                drawingModes: ['polygon'],
            },
            polygonOptions: {
                clickable: true,
                editable: true,
                zIndex: 1
            }

        });


        google.maps.event.addListener(self.drawingManager, 'overlaycomplete', function (event) {

            var arrayLatLng = event.overlay.getPath().getArray();

            console.log("arrayLatLNg: " + arrayLatLng);

            if (self.nizOverlay == undefined) {
                self.nizOverlay = [];
            }

            self.nizOverlay.push(event.overlay);

            if (self.nizPoligona == undefined) {
                self.nizPoligona = [];
            }

            self.nizPoligona.push(arrayLatLng);
            console.log("nizPOli: " + self.nizPoligona);

        });

        self.drawingManager.setMap(self.map);

    }

    getArrays() {
        console.log("get arrays");
        this.isLoading = true;
        this.sviPodtipovi = this.getSubtypesById();
        this.proizvodjaci = this.getProducers();

    }

    getTypes(id): Vrsta[] {
        console.log(this.user.id + " get tajps");
        this.http.get(`types/getTypes/${id}`)
            .map(res => res.json())
            .subscribe(data => {
                this.tipovi = data as Vrsta[];
                console.log(data + " wa wa wa");
            },
            err => console.log(err),
            () => console.log('Completed'));

        return this.tipovi;
    }

    getSubtypesById(): any {
        this.http.get(`types/getSubtypes`)
            .map(res => res.json())
            .subscribe(data => {
                this.sviPodtipovi = data;

            },
            err => console.log(err),
            () => console.log('Completed'));


        console.log(" pa podtipovi" + this.sviPodtipovi);
        return this.sviPodtipovi;
    }

    getProducers(): Proizvodjac[] {
        this.http.get(`types/getProducers`)
            .map(res => res.json())
            .subscribe(data => {
                this.proizvodjaci = data as Proizvodjac[];

            },
            err => console.log(err),
            () => console.log('Completed'));


        return this.proizvodjaci;
    }


    onSelect(typeID) {
        console.log(this.sviPodtipovi[0].idType);
        this.podtipovi = this.sviPodtipovi.filter(x => x.idType == typeID);
        console.log("asd dda" + this.podtipovi);
        if (this.podtipovi.length > 0)
            this.ddVisible = true;
        else
            this.ddVisible = false;
    }



}



