import { Component, OnInit } from '@angular/core';
import { Headers, Http } from '@angular/http';
@Component({
  selector: 'app-new-msg',
  templateUrl: './new-msg.component.html',
  styleUrls: ['./new-msg.component.css']
})
export class NewMsgComponent implements OnInit {

  constructor(private http: Http) { }
  porukaPoslata:boolean = false;
  username:any = '';
  poruka:any = '';
  ngOnInit() {

  }
  sendMessage() {
    this.http.get(`messages/sendMessage/${this.username}/${this.poruka}`)
      .map(res => res.json())
      .subscribe(data => {
        if (data.success){
          this.porukaPoslata = true;
          this.username = '';
          this.poruka = '';
        }
        else
          this.porukaPoslata = false;
      },
      err => console.log(err),
      () => console.log('Completed'));

  }
}



