import { StatsWidgets } from './statsWidgets/stats-widgets';
import { UpdateParcelComponent } from './update-parcel/update-parcel.component';


@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    WeatherMapComponent,
    UserPage,
    StatisticsPage,
    AddWorker,
    AddPlantation,
    ChartComponent,
    UpdateUser,
    MapaComponent,
    UpdatePlantation,
    MeniComponent,
    AdminPageComponent,
    RequestOwnershipPageComponent,
    Tabs,
    Tab,
    AddTypes,
    InboxComponent,
    SentComponent,
    MsgComponent,
    NewMsgComponent,
    FilterPipe,
    ImageUploadComponent,
    PageNotFoundComponent,
    PageNotFoundDComponent,
    NotificationsComponent,
    StatsWidgets,
    UpdateParcelComponent

  ],
  imports: [
  
    JsonpModule,
    ImageUploadModule.forRoot(),
    LocalStorageModule.withConfig({
      prefix: 'my-app',
      storageType: 'localStorage'
    }),
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule,
    ChartsModule,
    DatePickerModule,
    ReactiveFormsModule,
  ],
  providers: [{ provide: LocationStrategy, useClass: HashLocationStrategy }, AuthGuard, AdminAuthGuard, PlantationService, PermService],
  bootstrap: [AppComponent]
})
export class AppModule { }



