import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Headers, Http } from '@angular/http';
import { LocalStorageService } from 'angular-2-local-storage';
import { MsgComponent } from '../msg/msg.component';
@Component({
  selector: 'app-sent',
  templateUrl: './sent.component.html',
  styleUrls: ['./sent.component.css']
})
export class SentComponent implements OnInit {


  poslatePoruke = [];
  selectedMessage:any;
  brojPoruka:any;
  user:any;
  constructor(private http: Http, private localStorageService: LocalStorageService, private router: Router) { }

  ngOnInit() {
    this.http.get(`sessions/LogovaniKorisnik`)
        .map(res => res.json())
        .subscribe(data => {
          this.user = data;
          if (this.user.success === undefined) {
              this.getMessages();
          }

        },
        err => console.log(err),
        () => console.log('Completed'));
  }


  getMessages() {
    this.http.get('messages/getSentMessages')
      .map(res => res.json())
      .subscribe(data => {
        this.poslatePoruke = data;
      },
      err => console.log(err),
      () => console.log('Completed'));

  }
  
  goToMessage(poruka:any){
    this.selectedMessage = {id:poruka.id};
    this.router.navigate(['./msg',this.selectedMessage]);
  }

}



